.c {
    height: 100vh;
    position: relative;
    

    .c-bg {
        height: 100%;
        width: 20px;
        background-color: rgb(115, 188, 115);
        position: absolute;
    }

    .c-wrapper {
        padding: 50px;
        display: flex;

        .c-left {
            flex: 1;

            .c-title {
                font-size: 60px;
                width: 100%;
            }

            .c-info-item {
                display: flex;
                align-items: center;
                margin: 50px 0px;
                font-weight: 300;
                width: 70%;

                .c-icon {
                    width: 30px;
                    height: 30px;
                    margin-right: 20px;
                }
            }
            
            
        }
        
        .c-right {
            flex: 1;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            .c-desc{
                height: 10vh;
                width: 100%;

            }

            .c-thank{
                padding: auto;
                color: rgb(115, 188, 115);
            }


            form {
                margin-top: 5%;

                input {
                    width: 50%;
                    height: 50px;
                    border: none;
                    border-bottom: 1px solid darkgreen;
                    margin: 10px 0px;
                    font-size: 14px;
                    padding-left: 10px;

                }

                textarea {
                    width: 100%;
                    margin: 10px 0px;
                    font-size: 14px;
                    padding-left: 10px;
                }

                button {
                    border: none;
                    background-color: rgb(115, 188, 115);
                    padding: 14px;
                    font-weight: 500;
                    cursor: pointer;
                }
            }


        }
    }
}

@media screen and (max-width:480px){
    .c{
        height: auto;
        
        .c-wrapper{
            flex-direction: column;
        }
    }
}
@media screen and (max-width:850px){
    .c{
        height: auto;

        .c-wrapper{
            flex-direction: column;
        }
    }
}